import { useParticles } from "./hooks/useParticles";

type HeaderProps = {
  route: string;
};

const Header: React.FC<HeaderProps> = ({ route }) => {
  const { dispatch } = useParticles();
  return (
    <header>
      <button
        onClick={() => dispatch({ type: "SET_PARTICLES_AMOUNT", value: 0 })}
      >
        Remove particles
      </button>
      <button
        onClick={() => dispatch({ type: "SET_PARTICLES_AMOUNT", value: 6 })}
      >
        Reset
      </button>
      <input
        type="number"
        onChange={(e) =>
          dispatch({
            type: "SET_PARTICLES_AMOUNT",
            value: parseInt(e.target.value) as number,
          })
        }
      />
    </header>
  );
};
export default Header;
